.switch {
  width: 150px;
  height: 50px;
  text-align: center;
  -o-transition: all .2s ease;
  background: #4cd964;
  border-radius: 25px;
  margin: 0 15px;
  transition: all .2s;
  position: relative;
}

.switch span {
  width: 20px;
  height: 4px;
  -o-transition: all .2s ease;
  background: #fff;
  border-radius: 2px;
  margin: -2px 0 0 -4px;
  transition: all .2s;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: rotate(-45deg);
}

.switch span:after {
  content: "";
  width: 4px;
  height: 12px;
  -o-transition: all .2s ease;
  background: #fff;
  border-radius: 2px;
  margin-top: -8px;
  transition: all .2s;
  display: block;
  position: absolute;
}

input[type="radio"] {
  display: none;
}

.switch label {
  cursor: pointer;
  color: rgba(0, 0, 0, .2);
  width: 70px;
  -o-transition: all .2s ease;
  font-size: 50px;
  line-height: 39px;
  transition: all .2s;
}

label[for="yes"] {
  height: 20px;
  position: absolute;
  left: 0;
}

label[for="no"] {
  position: absolute;
  right: 0;
}

#no:checked ~ .switch {
  background: #06f;
}

#no:checked ~ .switch span {
  background: #fff;
  margin-left: -8px;
}

#no:checked ~ .switch span:after {
  height: 20px;
  background: #fff;
  margin-top: -8px;
  margin-left: 8px;
}

#yes:checked ~ .switch label[for="yes"], #no:checked ~ .switch label[for="no"] {
  color: #fff;
}

